*,*:after,*:before{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #000;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.cardContainer{
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;
  font-size: 50px;
  overflow: hidden;
}
.card{
  text-align: center;
  position: relative;
  transform: translateY(-80px);
  
}

.card span{
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}

.inputAnswer{
  width: 40%;
  font-size: 60px;
  text-align: center;
  background-color: #eee;
  border: none;
  height: 80px;
  display: block;
  margin: 0 auto;
}
.inputAnswer:focus{
  outline:none;
}

.gameContainer{
  width:100%;
  max-width: 1000px;
  margin: 0 auto;
}